import choseDayBar from 'components/chose-day-bar';
import { timeSheetQuery } from '@/api/time-sheet';
import Swiper from '../../../static/swiper-3.3.1.min.js'
import {schedulingInfo} from '@/api/scheduling'

export default {
  name: 'timeSheet',
  components: {choseDayBar},
  data() {
    return {
      weekList: ["一", "二", "三", "四", "五", "六", "日"],
      blankList: "",//空白的格子数
      dayCount: "",//每月的天数
      typeList: [
        {type: "请假"},
        {type: "出差"},
        {type: "加班"},
        {type: "休息"},
        {type: "考勤异常"},
      ],
      choseSpan: false,
      choseFlag: "",
      choseNowDay: "",//当前日期 精确到天/月
      sheetDetailList: "",//整月数据
      sheetList: "",
      abnormal: false,//是否异常 显示边框
      dayDetailList: "",//某天数据
      nowMonth: "",
      showNowDay: "",
      abnormalFlag: "",
      overTime: "",
      maxLength: "",
      option:{
        autoplay:false,
      },
      scheduledDate:"",
      backModel: [
        {},
        {},
        {},
        {},
        {},
      ],
    }
  },
  methods: {

    lunbo () {
      let that = this;
      new Swiper('.gallery-top', {
        spaceBetween: 10,
        initialSlide: 2,
        loop: true,
        loopedSlides: 5,
        touchRatio: 0.5,
        centeredSlides: true,
        autoHeight: true,
        effect: 'coverflow',
        slidesPerView: 1.2,
        coverflow: {
          rotate: 0,
          stretch: -13,
          depth: 70,
          modifier: 3,
          slideShadows: true
        },
        speed: 500,
        iOSEdgeSwipeDetection : true,//设置为true开启IOS的UIWebView环境下的边缘探测。如果拖动是从屏幕边缘开始则不触发swiper。
        preventLinksPropagation : true,

        //往前翻时触发方法
        onSlidePrevStart: function (swiper) {

          if(swiper.activeIndex===6||swiper.activeIndex===5){
            swiper.slideTo(2, 1000, false);
            //获取组件中的方法
            that.$refs.editUser.lastMonth();
          }else{
            that.$refs.editUser.lastMonth();
          }
        },
        //往后翻时触发方法
        onSlideNextStart: function (swiper) {
          //初始化页面时会加载onSlideNextStart方法，swiper.activeIndex===7是初始化加载slide的序号
          if(swiper.activeIndex===7){

          }else if(swiper.activeIndex===8||swiper.activeIndex===9){
            swiper.slideTo(2, 1000, false);//activeIndex>9以后数据加载不出来，此方法把activeIndex拉回9以内
            that.$refs.editUser.nextMonth();
          }else {
            that.$refs.editUser.nextMonth();
          }
        },


      });
    },
    getChange(msg){//切换年份月份
      this.choseNowDay = msg.slice(0, 4) + "-" + msg.slice(5, 7);
      this.nowMonth = msg.slice(5, 7);
      this.nowYear = msg.slice(0, 4);
      this.blankList = this.getBlank(this.nowYear, this.nowMonth - 1) - 1;
      this.dayCount = this.getDayCount(this.nowYear, this.nowMonth);
      this.getMonthList(this.choseNowDay);
      console.log("改变的值-----------" + this.choseNowDay);
    },
    choseDayBar(day, index){//点击天数显示
      this.choseFlag = index;
      if (this.choseNowDay.length === 10) {
        if (day < 10) {
          this.choseNowDay = this.choseNowDay.slice(0, 8) + "0" + day;
        } else {
          this.choseNowDay = this.choseNowDay.slice(0, 8) + day;
        }
      } else if (this.choseNowDay.length === 7) {
        if (day < 10) {
          this.choseNowDay = this.choseNowDay + "-" + "0" + day;
        } else {
          this.choseNowDay = this.choseNowDay + "-" + day;
        }
      }
      console.log("这是获取数据日期" + this.choseNowDay);
      this.getDayList(this.choseNowDay);
      this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.choseNowDay});
    },
    getBlank(year, month){//每月第一天周几
      let blankNum = new Date(year, month, "01");//下一个月一号是周几
      if (blankNum.getDay() === 0) {
        return 7;
      }
      console.log("这是函数计算天数" + blankNum.getDay());
      return blankNum.getDay();
    },
    getDayCount(year, month){//每月天数
      let dayNum = new Date(year, month, 0);
      return dayNum.getDate();
    },
    getDayList(){
      timeSheetQuery({
          "clockDate": this.choseNowDay,
        },
      ).then(response => {
        if (response.data && response.data.rows) {
          this.dayDetailList = response.data.rows;
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },
    getMonthList(param){
      timeSheetQuery({
          "clockDate": param,
        },
      ).then(response => {
        if (response.data && response.data.rows) {
          this.sheetDetailList = response.data.rows;
          this.maxLength = this.sheetDetailList.length;
          for (let i = 0; i < this.sheetDetailList.length; i++) {
            if (this.sheetDetailList[i].abnormal !== null) {
              this.abnormalFlag = i;
            }
            if (this.sheetDetailList[i].overTime === "Y") {
              this.overTime = i;
              console.log("加班日期" + this.overTime);
            }
          }
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },

    //排班接口
    getScheduling(params){
      schedulingInfo(params).then(response => {
        if (response.data && response.data.rows) {
          this.scheduledDate = response.data.rows[0].detailList;
          console.log("这里是sd"+this.scheduledDate);
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },

  },
  mounted() {
    this.nowMonth = this.nowMonth.slice(0, 4) + "-" + this.nowMonth.slice(5, 7);
    this.getMonthList(this.nowMonth);//初始获取一整月数据
    this.getDayList(this.choseNowDay);//获取某天数据
    console.log("当前月份" + this.nowMonth);
    console.log("当前日期" + this.choseNowDay);
    console.log("空白" + this.blankList);
    this.lunbo();
    this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.choseNowDay});
  },
  created: function () {
    let nowDate = new Date();
    let nowYear = nowDate.getFullYear();
    let nowMonth = nowDate.getMonth()+1;//月份0-11
    let nowDay = nowDate.getDate();
    if (nowDay < 10) {
      nowDay = "0" + nowDay;
    } else {
      nowDay = nowDate.getDate();
    }
    this.choseNowDay = nowYear + "-" + nowMonth + "-" + nowDay;//当前日期 年月日 用以显示当天数据
    this.dayCount = this.getDayCount(nowYear, nowMonth);//当前月天数 用以显示月天数
    this.blankList = this.getBlank(nowYear, nowMonth - 1) - 1;//月份是0-11 需要减一 空白是前面的空格减一
    if (nowMonth < 10) {
      this.nowMonth = nowYear + "年" + "0" + nowMonth + "月";
      console.log("单月" + this.nowMonth)
    } else {
      this.nowMonth = nowYear + "年" + nowMonth + "月";
      console.log("双月" + this.nowMonth)
    }
    console.log("传过去的"+this.nowMonth);
    return this.nowMonth;//传到组件中  格式为2017年11月06日
  }
}



